import React,{useState,useEffect} from "react";
import * as p5 from "p5";
import * as firebase from 'firebase';

const CompassSketch = () => {
  const [users,setUsers] = useState([]);
  useEffect(()=>{readUser()}, []);
  function readUser() {
      firebase.database().ref('responses').on('value',(snapshot)=>{
          let temp = [];
          if(snapshot.val() != null) {
              for (const [key, value] of Object.entries(snapshot.val())) {
                  temp[temp.length] = value;
              }
              setUsers(temp);
          }
      })
  }
  const Sketch = p5 => {
    let radius;	
    var cnv;
    var data = users;
    var dots = [];
    let gXrange = [];
    let gYrange = [];
    let size = 30;

    p5.setup = () => {
      let cnv = p5.createCanvas(p5.windowWidth, p5.windowHeight);
      var x = (p5.windowWidth - p5.width) / 2;
      var y = (p5.windowHeight - p5.height) / 2;
      cnv.position(x, y);
      p5.noStroke();
    };
  
    p5.draw = () => {
      p5.background(254,222,168);
      p5.ellipse(p5.width/2,p5.height/2,radius,radius);
       radius++;  
    };
  };
  
  useEffect(() => {
   new p5(Sketch);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  
  return (
     <></>
  );
 };
  
 export default CompassSketch;
